import Vue from 'vue'
import Router from 'vue-router'
import func from '@/utils/func'

Vue.use(Router)

const router = new Router({
    // mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            meta: {requireAuth: false},
            component: (resolve) => {
                require(['../views/home/home.vue'], resolve)
            }
        },
        {
            path: '/login',
            name: 'login',
            component: (resolve) => {
                require(['../views/auth/login.vue'], resolve)
            }
        },
        {
            path: '/register',
            name: 'register',
            component: (resolve) => {
                require(['../views/auth/register.vue'], resolve)
            }
        },
        {
            path: '/wx_author',
            name: 'wx_author',
            component: (resolve) => {
                require(['../views/auth/wx_author.vue'], resolve)
            }
        },
        {
            path: '/order',
            name: 'order',
            component: (resolve) => {
                require(['../views/order/order.vue'], resolve)
            }
        },
        {
            path: '/order/:id',
            component: (resolve) => {
                require(['../views/order/order_view.vue'], resolve)
            }
        }
    ]
})

// 判断是否需要登录权限 以及是否登录
router.beforeEach((to, from, next) => {
    const openid = func.getOpenid()
    if (!openid) {
        if (to.path === '/wx_author') {
            next()
        } else {
            func.setToUrl(to.fullPath)
            next('/wx_author')
        }
    }
    next()
    // if (to.matched.some(res => res.meta.requireAuth)) { // 判断是否需要登录权限
    //   if (localStorage.getItem('token')) { // 判断是否登录
    //     next()
    //   } else { // 没登录则跳转到登录界面
    //     next({
    //       path: '/login',
    //       query: {redirect: to.fullPath}
    //     })
    //   }
    // } else {
    //   next()
    // }
})

export default router



