let func = {
    getOpenid () {
        return localStorage.getItem('openid') || ''
    },
    setOpenid (value) {
        localStorage.setItem('openid', value)
    },
    setToUrl (value) {
        localStorage.setItem('to_url', value)
    },
    getToUrl () {
        const to_url = localStorage.getItem('to_url') || ''
        localStorage.removeItem('to_url')
        return to_url
    }
}

export default func



